          left: 50%;
          -webkit-transform: translateX(-50%);
                  transform: translateX(-50%);
          transition: all .5s ease-in;
        }  
        
        a.nav-laptop__link:hover {
          -webkit-transform: translateY(-3px) scale(1.1);
                  transform: translateY(-3px) scale(1.1);
          }
        
        /*a.nav-laptop__link:hover ~ .nav-laptop__link::before {*/
        /*  width: 90%;*/
        /*}  */
          
        .nav-laptop__link-active {
          position: relative;
        } 
        
        .nav-laptop__link-active::before {
          content: "";
          width: 90%; 
        }
        
      }

.navigation__logo {
  height: 4rem;
  position: absolute;
  top: 4rem;
  left: 2rem;
  z-index: 10000;
}

.navigation__checkbox {
  display: none;  
}

.navigation__button {
  background-color: #0d253c;
  background-color: var(--primary-color);
  height: 6rem;
  width: 6rem;
  position: absolute;
  top: 3rem;
  right: 2rem;
  border-radius: 10px;
  z-index: 2000;
  box-shadow: 0 1rem 3rem rgba(15, 15, 15, .2);
  text-align: center;
  cursor: pointer;
}

.navigation__background {
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  position: absolute;
  top: 3rem;
  right: 2rem;
  background-color: #102CFF;
  background-color: var(--secondary-color-bright);
  z-index: 1000;
  transition: all .3s cubic-bezier(0.86, 0, 0.07, 1);
  overflow: hidden;
}

.navigation__nav {
  height: 100%;
        
  position: absolute;
  top: -100%;
  left: 0;
  z-index: 1500;

  opacity: 0; 
  visibility: hidden;
  width: 0;
  transition: all .3s cubic-bezier(0.86, 0, 0.07, 1);
}

.navigation__list {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  list-style: none;
  text-align: left;
  width: 100%;
}

.navigation__item {
  margin: 1rem;
}

.navigation__link,
.navigation__link:link,
.navigation__link:visited {
  display: inline-block;
  font-size: 3.2rem;
  font-weight: 300;
  padding: .5rem 2.5rem;
  color: #F2F2F2;
  color: var(--white-color);
  text-decoration: none;
  font-weight: bold;
  text-underline-offset: 5px;
}

.navigation__checkbox:checked ~ .navigation__background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  border-radius: 0;
} 

.navigation__checkbox:checked ~ .navigation__nav {
  opacity: 1;
  visibility: visible;
  width: 100%;
  top: 0;
}

.navigation__checkbox:checked ~ .navigation__button {
  background-color: #386BED;
  background-color: var(--secondary-color);
}

.navigation__icon {
  position: relative;
  margin-top: 2.9rem;
}

.navigation__icon,
.navigation__icon::before,
.navigation__icon::after {
  width: 3rem;
  height: 3px;
  background-color: #F2F2F2;
  background-color: var(--white-color);
  display: inline-block;
}

.navigation__icon::before,
.navigation__icon::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all .2s;
}

.navigation__icon::before {
  top: -1rem;
}

.navigation__icon::after {
  top: 1rem;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
  top: 0;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
  top: 0;
}

.navigation__hire-me {
